.main-div {
    text-align: center;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: 30px;
}
figure {
    margin: 0 0 10px 0;
    color: white;
}
.add-items {
    position: relative;
}
input.add-item-input {
    position: relative;
    border: none;
    border-radius: 10px;
    padding: 5px 15px;
    width: 550px;
}
i.fa-solid.fa-plus.add-btn {
    position: absolute;
    transform: translate(-200%, 60%);
    cursor: pointer;
}
.each-item {
    position: relative;
}
h3.result-todo {
    position: relative;
    border: none;
    border-radius: 10px;
    padding: 5px 15px;
    width: 550px;
    text-align: left;
    background: #5640d3;
    color: white;
    margin: 10px 0;
    font-size: 18px;
    font-weight: 400;
    transition: all 0.3s ease;
}
i.fa-solid.fa-trash-can.add-btn {
    color: white;
    cursor: pointer;
}
.todo-btn {
    position: absolute;
    right: 2.3%;
    top: 4px;
}
h3.result-todo:hover{
    background: #fff;
    color: black;
    transition: all 0.3s ease;
}
.each-item:hover .todo-btn i.fa-solid.fa-trash-can.add-btn{
    color: red;
    transition: all 0.3s ease;
}
.deleteitems {
    margin: 20px 0;
}
